import React, {Component} from "react";
import Spinner from '../../../spinner/spinner';
import './allImgBase.css';
import './menu.css';

const Item = (props) => {
    const {item:{name, price, descr, img, id}} = props;
    return (
        <div className="menuCard__itemList">
            <div className="menuCard__photoIntro">
                <div className={`menuCard__photo ${id}${img}`}></div>
                <div className="menuCard__name">{name}</div>
                <div className="menuCard__price ">{`Prise ${price} $`}</div>
            </div>
            
            <div className="menuCard__description flex-center">
                <p>{descr}</p>
            </div>
        </div> 
    ) 
}

export default class MenuItem extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const {item} = this.props;
        const itemMenu = (this.props) ? <Item item={item}/> : <Spinner/>;
        return (
            <div className="menuCard__item">
                {itemMenu}
            </div>
        )
    }
    
    
}