import React, {Component} from 'react';
import keyGen from '../../../keyGenerator/keyGenerator';
import {SvgQuestion} from '../../social-icon/svgIcon';
import PopupHelp from '../../popup/popupHelp';
import './deliveryItem.css';


export default class DeliveryItem extends Component {
    constructor(props) {
        super(props);
        this.state = {
            order: {
                name: '',
                size: '',
                sum: ''
            }
        }
        this.onHelp = this.onHelp.bind(this);

    }
    componentWillUpdate() {
        this.render();
    }
   
    onHelp(e) {
        const help = document.querySelectorAll('.del-list-info svg');
        help.forEach((el, i) => {
            if(e.target.closest('.del-list-info svg') === el) {
                document.querySelectorAll('.help')[i].classList.add('show-help');
                document.querySelector('.delivery__item-list').style.overflow = 'hidden';
            }
        });
        
    }
    
    render() {  
        
        const {list, changeThing, changeSize, addDeliveryItem} = this.props;

        
        const createLi = list.map((item, i) => {
                const {name, price, descr, size, num, selected} = item;
                
                return (
                    <li key ={keyGen()}>
                        <p>{`${i + 1}. ${name}`}</p>
                        <div onClick={(e)=>this.onHelp(e)} className='del-list-info flex-center'>
                            <SvgQuestion/>
                            <PopupHelp descr={descr}/>
                        </div>
                        <select onChange={() => changeSize(i)} className='del-select-size'>
                            <option value='1.1' selected={false || selected[0]}>small</option>
                            <option value='1.25' selected={false || selected[1]}>middle</option>
                            <option value='1.4' selected={false || selected[2]}>maxi</option>
                        </select>
                        <div className='del-quantity-items'>
                            <button onClick={()=> changeThing(-1, i)} className='flex-center'>-</button>
                            <div>{`${num}`}</div>
                            <button onClick={()=> changeThing(1, i)} className='flex-center'>+</button>
                        </div>
                        <div className='del-item-total flex-center'>{`${Math.floor(price * num * size)} $`}</div>
                        <button onClick={() => addDeliveryItem(i)} className='del-item-add flex-center'>Add</button>
                    </li> 
                );
            });

        
        return (
            <ul className='delivery__item-list-content'>
                {createLi}    
            </ul>
        )
    }
}