import React from 'react';
import './popupHelp.css';

const PopupHelp = ({descr}) => {

    return (
        <div className='help'>
            <button className='helpButton' onClick={() => {
                document.querySelectorAll('.help').forEach(el => el.classList.remove('show-help'));
                document.querySelector('.delivery__item-list').style.overflow = 'none';
            }}>X</button>
            {descr}
        </div>
    )
}
export default PopupHelp;