import React from 'react';
import {Home, About, Team, Menu, ContactUs, Delivery, HappyClient, Offers} from '../pages';
import Burger from '../navigation/burger';
import './App.css';

const AppfromTablet = ({tabBurger, displayWidth}) => {
    return (
        <div className='tabletContainer'>
            <Burger tabBurger={tabBurger}/>
            <div className='wrapper first'>
                <Home/>
            </div>
            <h2 className='titel'>About Us</h2>
            <div className='wrapper'>
                <About/>
            </div>
            <h2 className='titel'>Our Team</h2>
            <div className='wrapper'>
                <Team displayWidth={displayWidth}/>
            </div>
            <h2 className='titel'>Our Menu</h2>
            <div className='wrapper'>
                <Menu/>
            </div>
            <h2 className='titel'>Delivery</h2>
            <div className='wrapper del'>
                <Delivery/>
            </div>
            <h2 className='titel'>Our Happy Client Story</h2>
            <div className='wrapper'>
                <HappyClient/>
            </div>
            <h2 className='titel'>Our Offers</h2>
            <div className='wrapper'>
                <Offers/>
            </div>
            <h2 className='titel'>Contact Us</h2>
            <div className='wrapper'>
                <ContactUs/>
            </div>
        </div>
    )
}

export default AppfromTablet;