import React from 'react';
import './offers.css';
import './mobMinStyle.css'

const Offers = () => {
    const hiddenCard = (e) => {
        const card = document.querySelectorAll('.front');
        const back = document.querySelectorAll('.back');
        card.forEach((item, i) => {
            if(item === e.target) {
                item.classList.toggle('hidden');
                return (item.classList.contains('hidden')) ? back[i].classList.add('showText') : back[i].classList.remove('showText');
            }
            item.classList.remove('hidden');
        })
        
    }
    

    return (
        <div id='offers' onClick={(e) => hiddenCard(e)} className='offers-container'>
            <div  className='offers-card'>
                 <div  className="card1 front">
                    <div className='sale-box'>
                        summer sale
                    </div>
                </div>
                <div className='back center'>
                    <div className='clipPathForText'></div>
                    <h2>Offer1</h2>
                    <p>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Saepe eaque officiis ipsam ad autem reiciendis aliquid libero ex commodi placeat iure animi molestias mollitia nostrum quae, fugiat quod similique eveniet.</p>
                </div>
            </div>
            <div className='offers-card'>
                 <div className="card2 front">
                    <div className='sale-box'>
                        for kids
                    </div>
                </div>
                <div className='back center'>
                    <div className='clipPathForText'></div>
                    <h2>Offer2</h2>
                    <p>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Saepe eaque officiis ipsam ad autem reiciendis aliquid libero ex commodi placeat iure animi molestias mollitia nostrum quae, fugiat quod similique eveniet.</p>
                </div>
            </div>
            <div className='offers-card'>
                 <div className="card3 front">
                    <div className='sale-box'>
                        morning sale
                    </div>
                </div>
                <div className='back center'>
                    <div className='clipPathForText'></div>
                    <h2>Offer3</h2>
                    <p>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Saepe eaque officiis ipsam ad autem reiciendis aliquid libero ex commodi placeat iure animi molestias mollitia nostrum quae, fugiat quod similique eveniet.</p>
                </div>
            </div>
            <div className='offers-card'>
                <div className="card4 front">
                    <div className='sale-box'>
                        weekend sale
                    </div>
                </div>
                <div className='back center'>
                    <div className='clipPathForText'></div>
                    <h2>Offer4</h2>
                    <p>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Saepe eaque officiis ipsam ad autem reiciendis aliquid libero ex commodi placeat iure animi molestias mollitia nostrum quae, fugiat quod similique eveniet.</p>
                </div>
            </div> 
        </div>
    )
}

export default Offers;