import React, {Component} from 'react';
import DeliveryTabs from './deliveryTabs';
import DeliveryItem from './deliveryItem';
import DeliveryList from './deliveryList';
import gotService from '../../../services/gotService';
import {modalMessage, removeModal} from '../../popup/modalMessage';
import './delivery.css';

export default class Delivery extends Component {
    constructor(props) {
        super(props);
        this.gotService = new gotService();
        this.state = {
            id: '',
            list: null,
            order: null,
            sum: 0,
            result: null
        }
        this.gotMenu = this.gotMenu.bind(this);
        this.getIdTab = this.getIdTab.bind(this);
        this.changeThing = this.changeThing.bind(this);
        this.changeSize = this.changeSize.bind(this);
        this.addDeliveryItem = this.addDeliveryItem.bind(this);
        this.removeDeliveryItem = this.removeDeliveryItem.bind(this);
        this.submitOrder = this.submitOrder.bind(this);
    }
    componentWillUnmount(){
        this.setState({result: null});
    }

    async changeThing(num, index) {
        const {list} = this.state;
        let thingNum = {};
        let newList = [...list];
        
        await list.filter((item, i) => {
            if(i === index) {
               return Object.assign(thingNum , item);
            }
        });
        const sum = thingNum.num + num;
        if(sum <= 0) {
            newList[index].num = 1;
            return this.setState({list: newList});
        } else {
            newList[index].num = sum;
            return this.setState({list: newList});
        }
    }

    changeSize(index) {
        const selectedLi = document.querySelectorAll('.del-select-size')[index];
        const selectedOption = selectedLi.selectedIndex;
        const value = selectedLi[selectedOption].value;
        let newList = [...this.state.list];
        newList[index].selected[selectedOption] = true;
        newList[index].size = value;
        this.setState(({list: newList}));
    }

    async gotMenu(name) {
        let newList = [];
        await this.gotService.getMenuItem(name)
            .then(data =>  {
               return data.map((item, i) => {
                    let itemList = {
                        name: item.name,
                        price: item.price,
                        descr: item.descr,
                        size: 1.1,
                        num: 1,
                        selected: [false, false, false ]
                    }
                    newList.push(itemList);
                });
            });
        this.setState({
            list: newList
        }); 
        
    }

    async getIdTab(e) {
        if(e.target.dataset.id === 'tabsContainer' || e.target === document.querySelector('.del-tabs h2')) {
            return this.setState({
                list: null
            }); 
        }else{
            const idTab = e.target.closest('.del-tabs__icon').dataset.id;
            await this.gotMenu(idTab);  
        }
        
    }

    removeDeliveryItem(index) {
        this.setState(({order}) => {
            const befor = order.slice(0, index);
            const after = order.slice(index + 1);
            const newArr = [...befor, ...after];
            let newSum = 0;
            newArr.map(item => newSum = newSum + item.total)
            return {
                order: newArr,
                sum: newSum
            }
        });
    }
    addDeliveryItem(index) {
        let newOrder = []; 
        if (this.state.order) {
            newOrder = [...this.state.order]
        }  
        const newList = {...this.state.list[index]};
        const newData = {
            name: newList.name,
            num: newList.num,
            total: Math.floor(newList.num * newList.size * +newList.price)
        }
        newOrder.push(newData);
        let deliveryTotalPrice = 0
        newOrder.map((item) => deliveryTotalPrice = deliveryTotalPrice + item.total);
        this.setState({
            order : newOrder,
            sum: deliveryTotalPrice
        });
    }
   
    async submitOrder(e) {
        e.preventDefault();
        const phone = document.querySelector('.delivery-list__submit-phone');

        const {order, sum} = this.state;
        const newOrder = [
            {
                phoneNumber: phone.value,
                totalPrice: sum
            }
        ];
        await order.map((item) => {
            const data = {
                name: item.name,
                num: item.num,
                total: item.total
            }
            return newOrder.push(data);
        });
        const delivery = {
            id: Date(),
            data: newOrder
        }
        const result = () => {
            const data = JSON.stringify(delivery);
            let promise = new Promise((res, rej) => {
                (data) ? res(data) : rej("Error");
            });
            return promise;
        }
        await result()
            .then( (data) => {
                modalMessage('.delivery', 'sucsses');
                this.setState({result: data})
                removeModal('.modalMes', 2000);
            })
            .catch(() => {
                modalMessage('.delivery', "error");
                removeModal('.modalMes', 2000);
            })
            .finally(() => {
                phone.value = '';
                this.setState({
                    id: '',
                    order: null,
                    sum: 0
                })
            });
        
    }

    render() {
        const {list, order, sum, result} = this.state;
        const showList = (list === null) ? 
            <h2>First choose a food</h2> : 
            <DeliveryItem 
                list={list} 
                changeThing={this.changeThing} 
                changeSize={this.changeSize}
                addDeliveryItem={this.addDeliveryItem}
                />;
        const deliveryList = (order === null) ? <h2>First choose a food</h2> : <DeliveryList orderList={order} removeDeliveryItem={this.removeDeliveryItem}/>
        const typeBtn = (order === null) ? 'button' : 'submit';
        return (
            <div id='delivery' className='delivery del-width'>
                <div className='tabs-list__wrapper flex-center cl-w'>
                    <DeliveryTabs getIdTab={this.getIdTab}/>
                    <div className='delivery__item-list'>
                        {showList}
                    </div>
                </div>
                <form onSubmit={(e) => this.submitOrder(e)} className='delivery-list__wrapper cl-w'>
                    <h2>Your order list</h2>
                    {deliveryList}  
                    <div className='delivery-submit'>
                        <div className='delivery-list__total-price'>{`${(sum !== 0 || order !== null) ? sum : 0} $`}</div>
                        <input  
                            className='delivery-list__submit-phone' 
                            placeholder="Your Phone number" 
                            type="tel" 
                            required/>
                        <button type={typeBtn} className='submit-order'>Submit Order</button>  
                    </div>
                    <div className='result_onSubmit'>
                        <h3>What be send on server:</h3>
                        {(result !== null) ? <p>{`${result}`}</p> : false}
                    </div>
                </form>
                
            </div>
        )
        
    }
}