import React, {Component} from "react";
import gotService from '../../../services/gotService';
import {SvgArrowRight, SvgArrowLeft} from '../../social-icon/svgIcon';
import MenuNav from './menuNav';
import MenuItem from './menuItem';
import './menu.css';


export default class Menu extends Component {
    constructor(props){
        super(props);
        this.gotService = new gotService();
        this.state = {
            index: 1,
            dataId: null,
            itemLength: null,
            menu: [],
            item: []
        }
        this.showIdTab = this.showIdTab.bind(this);
        this.showMenu = this.showMenu.bind(this);
        this.showItem = this.showItem.bind(this);
        this.currentMenu = this.currentMenu.bind(this);
        this.resetState = this.resetState.bind(this);
        this.gotMenu = this.gotMenu.bind(this);
    }

    componentDidMount() {
        this.currentMenu();
    }
    componentWillUnmount() {
        this.resetState()
    }

    resetState() {
        this.setState({
            index: 1,
            dataId: null,
            itemLength: null,
            menu: [],
            item: []
        })
    }

    async gotMenu(name) {
        let allIdMenu = [];
        await this.gotService.getMenuItem(name).then(data => allIdMenu = data);
        this.setState({
            menu: allIdMenu,
            itemLength: allIdMenu.length
        }); 
    }

    async currentMenu(dataId = 'pizza') {
        await this.gotMenu(dataId);
        this.showMenu(this.state.index);
    }

    showMenu(num) {
        this.setState({
            item: this.state.menu[num - 1]
        });     
    }
    showIdTab(event) {
        event.preventDefault();
        const tabId = event.target.closest('li').dataset.id;
        this.setState({
            index: 1,
            itemLength: null,
            menu: [],
            item: [],
            dataId: tabId
        });
        this.currentMenu(tabId);
    }
    async showItem(num) {
        let slide =   Object.assign({}, this.state);
        slide.index = slide.index + num;
        const {itemLength} = this.state;

        if(slide.index > itemLength) {
            slide.index = 1;
        }
        if(slide.index < 1) {
            slide.index = itemLength;
        }

        this.setState({index: slide.index});
        this.showMenu(slide.index);
    }

    render() {
        const {index, dataId, itemLength, item} = this.state;
        
        return (
            <div id='menu' className="menu__mainContainer">
                <MenuNav showIdTab ={this.showIdTab} dataId={dataId} />
                <div className="menu__Container">
                    <div className="menu__buttonContainer">
                        <button onClick={() => this.showItem(-1)} className="flex-center">
                            <SvgArrowLeft/>
                        </button>
                        <button onClick={() => this.showItem(1)} className="flex-center">
                            <SvgArrowRight/>
                        </button>
                        <div className="menu__slideCounter flex-center">
                           <p>{`${index} / ${itemLength}`}</p> 
                        </div>
                    </div>
                    <MenuItem item={item}/>
                </div>
            </div>
        )

    }


}