import React from 'react';

import './burger.css';

const Burger = ({tabBurger}) => {
    return (
        <div onClick={(e) => tabBurger(e)}  className="navbarBurger__container">
			 <button data-id="burger" className="burger">
				<div data-id="burgerIcon" className="burgerIcon"></div>
			</button>	
			<div className="burgerNav">
                <a href='#home' >Home</a>
                <a href='#about' >About</a>
                <a href='#team'>Team</a>
                <a href='#menu'>Menu</a>
                <a href='#delivery'>Delivery</a>
                <a href='#happy-client'>HappyClient</a>
                <a href='#offers'>Offers</a>
                <a href='#contactUs'>ContactUs</a>	
			</div>
		</div>
    )
}
export default Burger;