import React from "react";
import {SvgCupcake, SvgPasta, SvgSandwich, 
    SvgPizza, SvgIceCream, SvgCoffee, SvgSoda, 
    SvgTeaCup, SvgPancake} from '../../social-icon/svgIcon';
import './menu.css';

const MenuNav = ({showIdTab, dataId}) => {
    const li = document.querySelectorAll('.menu__navigationContainer ul li');
    if(dataId) {
       const liData = document.querySelector(`.menu__navigationContainer ul li[data-id=${dataId}]`);
       li.forEach((el) => {
            return el.classList.remove('active');
        });
        liData.classList.add('active'); 
    }
    


    return (
        <div  className="menu__navigationContainer">
            <ul onClick={(e) => showIdTab(e)}>
                <li data-id = 'pizza' className='active'>
                    <div  className="menu__icon flex-center">
                        <SvgPizza/>
                    </div>
                </li>
                <li data-id = 'pasta'>
                    <div className="menu__icon flex-center">
                        <SvgPasta/>
                    </div>
                </li>
                <li data-id = 'sandwich'>
                    <div className="menu__icon flex-center">
                        <SvgSandwich/>
                    </div>
                </li>  
                <li data-id = 'cupcake'> 
                    <div className="menu__icon flex-center">
                        <SvgCupcake/>
                    </div>
                </li>  
                <li data-id = 'pancake'>
                    <div className="menu__icon flex-center">
                        <SvgPancake/>
                    </div>
                </li> 
                <li data-id = 'iceCream'>
                    <div className="menu__icon flex-center">
                        <SvgIceCream/>
                    </div>
                </li>    
                <li data-id = 'coffee'>
                    <div className="menu__icon flex-center">
                        <SvgCoffee/>
                    </div>
                </li>  
                <li data-id = 'tea'>
                    <div className="menu__icon flex-center">
                        <SvgTeaCup/>
                    </div>
                </li> 
                <li data-id = 'soda'>
                    <div className="menu__icon flex-center">
                        <SvgSoda/>
                    </div>
                </li>          
            </ul>
        </div>
    )
}

export default MenuNav;