import React, {Component} from 'react';
import {BrowserRouter as Router, Route} from 'react-router-dom';
import './App.css';
import NavMenu from '../navigation/navigation';
import AppfromTablet from './AppfromTablet';
import AppfromMob from './AppfromMob';
import {Home, About, Team, Menu, ContactUs, Delivery, HappyClient, Offers} from '../pages';

export default class App extends Component {
    constructor(props) {
        super(props);
        this.tabBurger = this.tabBurger.bind(this);
        this.removeClass = this.removeClass.bind(this);
        this.state = {
            width: 0,
            agentMatch: ''
        }
        this.useWindowSize = this.useWindowSize.bind(this);
    }
    componentDidMount() {
        window.addEventListener('resize', this.useWindowSize);
        const agent = window.navigator.userAgent.match(/mobile|Android|ip(hone|od)|ipad|blackberry|Phone|Tablet/gim);
        const res = (agent === null) ? false : true;
        return this.setState({
            width: window.innerWidth,
            agentMatch: res
        });
    }
    
    componentWillUnmount() {
        window.removeEventListener('resize', this.useWindowSize);
    }
    useWindowSize() {
        return this.setState({width: window.innerWidth});
    }
    removeClass() {
        const burger = document.querySelector('.burger');
        burger.classList.remove('burger__active-hide');
    }
   
    tabBurger(e) {
        const burger = document.querySelector('.burger');
        const burgerNav__Link = document.querySelectorAll('.burgerNav a');
        let result = '';
        const hidden = () => {
            burger.classList.remove('burger__active');
            burger.classList.add('burger__active-hide');
            setTimeout(() => {
                this.removeClass();
            }, 1900);
        }
        const show = () => {
            burger.classList.remove('burger__active-hide');
            burger.classList.add('burger__active');
        } 
        burgerNav__Link.forEach(el => {
            if(el === e.target) {
              result = hidden();
            }
        });
        if (e.target.closest('.burger') === burger) {
            result = (burger.classList.contains('burger__active')) ? hidden() : show();
        }
        return result;
    }
    render() {
        const {width, agentMatch} = this.state;
        const AppfromDesktop = ({displayWidth}) => {
            return (
                <div className='container'>
                    <Route path='/violet-app' exact component={Home}/>
                    <Route path='/about'  component={About}/>
                    <Route path='/team'  component={() => <Team displayWidth={displayWidth}/>}/>
                    <Route path='/menu'  component={Menu}/>
                    <Route path='/delivery'  component={Delivery}/>
                    <Route path='/happy-client'  component={HappyClient}/>
                    <Route path='/offers'  component={Offers}/>
                    <Route path='/contact'  component={ContactUs}/>
                </div>
            )
        }
        const desktop = (width > 1000 || width === 0 && agentMatch === false) ? <AppfromDesktop displayWidth={width}/> : false;
        const tablet = (width < 1001 && width > 750 || agentMatch === true) ? <AppfromTablet  tabBurger={this.tabBurger} displayWidth={width}/> : false;
        const mobile = (width < 751 && width !== 0) ? <AppfromMob  tabBurger={this.tabBurger} displayWidth={width}/> : false;
        return (
            <Router>
                <div className="app">
                    {<NavMenu/>}
                    {desktop}
                    {tablet}
                    {mobile}  
                </div> 
            </Router>
            
        );
    }
  
}

