import React from 'react';
import {SvgCupcake, SvgPasta, SvgSandwich, 
    SvgPizza, SvgIceCream, SvgCoffee, SvgSoda, 
    SvgTeaCup, SvgPancake} from '../../social-icon/svgIcon';
import './delivery.css';
import './deliveryTabs.css';

const DeliveryTabs = ({getIdTab}) => {
    return ( 
        <div onClick={(e)=>getIdTab(e)} data-id='tabsContainer'  className='del-tabs'>
            <div data-id='pizza'  className="del-tabs__icon flex-center">
                <SvgPizza/>
            </div>
            <div data-id='pasta' className="del-tabs__icon flex-center">
                <SvgPasta/>
            </div>
            <div data-id='sandwich' className="del-tabs__icon flex-center">
                <SvgSandwich/>
            </div>
            <div data-id='cupcake' className="del-tabs__icon flex-center">
                <SvgCupcake/>
            </div>
            <div data-id='pancake' className="del-tabs__icon flex-center">
                <SvgPancake/>
            </div>
            <div data-id='iceCream' className="del-tabs__icon flex-center">
                <SvgIceCream/>
            </div>
            <div data-id='coffee' className="del-tabs__icon flex-center">
                <SvgCoffee/>
            </div>
            <div data-id='tea' className="del-tabs__icon flex-center">
                <SvgTeaCup/>
            </div>
            <div data-id='soda' className="del-tabs__icon flex-center">
                <SvgSoda/>
            </div>
        </div>       
    )
}

export default DeliveryTabs;