import React from 'react';
import {SvgTwitter,SvgFacebook, SvgInstagram} from '../../social-icon/svgIcon';
import './contactSocial.css';

const ContactSocial = () => {
    return (
        <div className='contactSocial'>
            <section className="social">
                <div className='contact-wrapper'>
                    <div className='contact-clipPath-wrapper'>
                        <div className="contact__sociaIconlContainer">
                            <a href="#" target="_blank">
                                <SvgFacebook/>
                            </a>
                        </div>
                        <p>Your facebook page</p>
                    </div>
                </div>
                <div className='contact-wrapper'>
                    <div className='contact-clipPath-wrapper'>
                        <div className="contact__sociaIconlContainer">
                            <a href="#" target="_blank">
                                <SvgInstagram/>
                            </a>
                        </div> 
                        <p>@YourInsta</p>
                    </div>
                </div>
                <div className='contact-wrapper'>
                   <div className='contact-clipPath-wrapper'>
                        <div className="contact__sociaIconlContainer">
                            <a href="#" target="_blank">
                                <SvgTwitter/>
                            </a>
                        </div>
                        <p>@YourTwitter</p>
                    </div> 
                </div>
            </section>
        </div>
        
    )
}

export default ContactSocial;