import React from 'react';
import {SvgMail, SvgFacebookNoCircle, SvgInstagramNoCircle} from '../../social-icon/svgIcon';
import './home.css';

const Home = () => {
    return (
        <div id="home" className='home'>
            <div className='home-social'>
                   <SvgFacebookNoCircle/>
                   <SvgInstagramNoCircle/>
                   <SvgMail/>
                </div>
            <div className="left-box flex-center">
                <h1>Violet</h1>
            </div>
            <div className="right-box cover"></div>            
        </div>
    )
}

export default Home;
