import keyGen from '../keyGenerator/keyGenerator';
import dataBase from './serviceData';

export default class GotService {
    constructor() {
        this._apiBase = 'http://localhost:3004/';
        this.postData = this.postData.bind(this);
        this.getResource = this.getResource.bind(this);
        this.getAllTeam = this.getAllTeam.bind(this);
        this.getAllMenu = this.getAllMenu.bind(this);
        this.getMenuItem = this.getMenuItem.bind(this);
        this.getStaticData = this.getStaticData.bind(this);
    }   
    
    postData = async (data, url = 'http://localhost:3004/delivery') =>{
        const res = await fetch(url , {
            method: "POST",
            headers: { 
                'Content-type': 'application/json; charset=utf-8'
            },
            body: data
        });
        return await res.json();
    };
    getStaticData = async(data) => {
        let promise = new Promise(function(res,rej) {
            (data) ? res(data) : rej(new Error(`Could not work getStaticData`))
        });
        return promise;
    } 
    getResource = async (url) => {
        const res = await fetch(`${this._apiBase}${url}`);

        if(!res.ok) {
            throw new Error(`Could not fetch ${this._apiBase}${url}, received ${res.status}`);
        }
        return await res.json();
    }

    getAllTeam = async () => {
        /* const res = await this.getResource('team'); */
        const res = await this.getStaticData(dataBase['team']);
        return res.map(this._transformTeam);
    }

    getAllMenu = async () => {
        /* const res = await this.getResource('menu'); */
        const res = await this.getStaticData(dataBase['menu']);
        let allMenu = {};
        res.map((item) => {
           return allMenu = {...allMenu,...item};
        });
        return allMenu;
    }

    getMenuItem = async (name) => {
        let menu = []; 
        await this.getAllMenu().then(data => menu = data[name]);
        return menu;
    }


    isSet = (data) => {
        if(data) {
            return data;
        } else {
            return 'no data :(';
        }
    }

    _extractId = () => {
        const key = keyGen()
        return key;
    }
 
    _transformTeam = (person) => {
        return {
            id: this._extractId(),
            name: this.isSet(person.name),
            prof: this.isSet(person.prof),
            photo: this.isSet(person.photo),
            descr: this.isSet(person.descr)
        }
    }

    _transformMenu = (item) => {
        return {
            key: this._extractId(),
            id: this.isSet(item.id),
            name: this.isSet(item.name),
            price: this.isSet(item.price),
            img: this.isSet(item.img),
            descr: this.isSet(item.descr) 
        }
    }
}