import React from 'react';
import {SvgPhone, SvgMail, SvgMap, 
        SvgOpen, SvgWorld} from '../../social-icon/svgIcon';
import './contactAdressList.css';


const ContactAdress = () => {
    return (
        <div className="contactAdressList">
            <ul>
                <li>
                    <SvgMap/>
                    <a className="a__contact" href="#">World, Earth</a>
                </li>
                <li>
                    <SvgPhone/>
                    <a className="a__contact" href="#">+7 (777) 777-777-777</a>
                </li>
                <li>
                    <SvgMail/>
                    <a className="a__contact" href="#">Our@website.com</a>
                </li>
                <li>
                    <SvgWorld/>
                    <a className="a__contact" href="#">http://www.website.com</a>
                </li>
                <li>
                    <SvgOpen/>
                    <a className="a__contact" href="#">9:00 - 22:00</a>
                </li>
            </ul>
        </div>
    )
}

export default ContactAdress;