
const keyGen = (keyLenght = 10) => {
    const str = "QWERTYUIOPASDFGHJKLZXCVBNM0123456789";
    const upper = str.match(/\D/gm);
    const lower = str.toLowerCase().match(/\D/gm);
    const num = str.match(/\d/gm);
    let id = [];
    
    for(let i = 0; i < keyLenght; i++) {
        let random = Math.floor(Math.random() * Math.floor(10));
        if(random >= 0 && random < 3) {
            id.push(upper[random]);
        } else if(random >= 4 && random < 7) {
            id.push(lower[random]);
        } else {
            id.push(num[random]); 
        }
    }
    return id.reduce((sum, num) => `${sum}${num}`); 
}

export default keyGen;