import React  from 'react';

import './contactForm.css';

const ContactForm = ({sendForm}) => {
    return (
        <div className="contactForm__container">  
            <form onSubmit={(e) => sendForm(e)} id="contact" action="" method="post">
                <h3>Contact Form</h3>
                <h4>Contact us</h4>
                <fieldset>
                    <input  placeholder="Your name" data-name="name" type="text" tabIndex="1" required/>
                </fieldset>
                <fieldset>
                    <input  placeholder="Your Email Address" data-name="email" type="email" tabIndex="2" required/>
                </fieldset>
                <fieldset>
                    <textarea  placeholder="Type your message here...." data-name="message" tabIndex="3" required></textarea>
                </fieldset>
                <fieldset>
                    <button name="submit" type="submit" id="contact-submit" data-submit="...Sending">Submit</button>
                </fieldset>
            </form>
        </div>
    )
}

export default ContactForm; 

