import React, {Component}  from 'react';
import gotService from '../../../services/gotService';
import {SvgTwitter, SvgFacebook} from '../../social-icon/svgIcon';
import keyGen from '../../../keyGenerator/keyGenerator';
import './team.css';


export default class Team extends Component {
    constructor(props) {
        super(props);
        this.gotService = new gotService();
        this.state = {
            team: [],
            slideIndex: 0
        };
        this.teamBase = this.teamBase.bind(this);
        this.showTeamCard = this.showTeamCard.bind(this);
        this.dotOpacity = this.dotOpacity.bind(this);
        this._isMounted = false;
    }

    componentDidMount() {
        this._isMounted = true;
        if (this._isMounted) {
            this.teamBase();
        }
    }
    componentWillUnmount() {
        this._isMounted = false;
    }
    async teamBase() {
        await this.gotService.getAllTeam()
        .then( (data) => {
            let teamArr = []; 
            data.map((item, i) => {
                return teamArr[i] = item;
            }); 
            return this.setState(() => {
                return {
                    team: teamArr
                }
             });
         });
    }
    showTeamCard(num) {
        const {displayWidth} = this.props;
        
        const slideWrapper = document.querySelector('.team__card-container');  
        const slide = document.querySelectorAll('.team__card');
        const dots = document.querySelectorAll('.carousel-indicator li');
        let old = Object.assign({}, this.state);
        let length = slide.length;
        let index = old.slideIndex + num;
        const size = (displayWidth > 500 || displayWidth === 0) ? -500 : -400;
        if(index >= length) {
            slideWrapper.style.cssText = `transform: none;`;
            index = 0;
            this.dotOpacity(dots, index);
            return this.setState({slideIndex: 0});
        } 
        if (index < 0) {
            index = length - 1;
        } 
        this.dotOpacity(dots, index);
        slideWrapper.style.cssText = `transform: translateX(${size * index}px);`;
        return this.setState({slideIndex: index});

    }
    dotOpacity(obj, slideIndex) {
        obj.forEach(dot => dot.style.opacity = '0.5');
        obj[slideIndex ].style.opacity = 1;
    }
    render() {
        const {team} = this.state;
        const person = team.map((person) => {
            const {name, id, descr, prof, photo} = person;
            return (
                <div key = {id}>
                    <div className="team__card"> 
                        <div className={`team__card-img cover ${photo}`}>
                            <div className='team__social'>
                                <SvgFacebook/>
                                <SvgTwitter/>
                            </div>
                        </div>
                        <h2>{name}</h2>
                        <h3>{prof}</h3>
                        <p>{descr}</p>
                    </div>
                </div>
            )
        });
        const dots = team.map(() => {
            return (
                <li key ={keyGen()}></li>
            )
        });
        
        return (
            <div id='team' className="team">
                <div className='team-slider-wrapper'>
                    <div className='team__card-container'>
                        {person}
                    </div> 
                    <ul className='carousel-indicator'>
                        {dots}
                    </ul>
                    <button className='team__slider-btn-prev' onClick={() => this.showTeamCard(-1)}></button>
                    <button className='team__slider-btn-next' onClick={() => this.showTeamCard(1)}></button>
                </div>
            </div>
        );
    }
}