import React, { Component } from 'react';
import './modalMessage.css';

const modalMessage = (formSelector, mes) => {
    const form = document.querySelector(formSelector);
    const statusMessage = document.createElement('div');
    const message = {
        loading: '...Loading',
        sucsses: '!     ',
        failure: '-   ...'
    };
    let result = message.loading;
    if(mes === 'sucsses') {
        result = message.sucsses;
    }
    if(mes === 'error') {
        result = message.failure;
    }
    
    statusMessage.classList.add('modalMes');
    statusMessage.innerText = result;
    form.insertAdjacentElement('afterend', statusMessage);
    return statusMessage;
}
const removeModal = (selector, timer) => {
    setTimeout(() => {
        const elem = document.querySelector(selector);
        elem.remove();
    }, timer)
}

export {modalMessage, removeModal};