import React, {Component} from 'react';
import ContactForm from './contactForm';
import ContactSocial from './contactSocial';
import ContactAdress from './contactAdressList';
import gotService from '../../../services/gotService';
import keyGen from '../../../keyGenerator/keyGenerator';
import {modalMessage, removeModal} from '../../popup/modalMessage';
import './contact.css';

export default class ContactUs extends Component {
    constructor(props) {
        super(props);
        this.gotService = new gotService();
        this.state = {
            form: '',
            result: null
        }
        this.showTabContent = this.showTabContent.bind(this);
        this.sendForm = this.sendForm.bind(this);
    }

    showTabContent(e) {
        e.preventDefault();
        const tabs = document.querySelectorAll('.contactUs__info-header-tab');
        const items = document.querySelectorAll('.contactUs__tabcontent');
        tabs.forEach(tab => {
            if(tab == e.target) {
                items.forEach(item => {
                    item.classList.remove('show-content'); 
                }); 
               return tab.nextElementSibling.classList.add('show-content');
            }
        });
    }
   
    async sendForm(e) {
        e.preventDefault();
        const name = document.querySelector('[data-name="name"]');
        const email = document.querySelector('[data-name="email"]');
        const messageForm = document.querySelector('[data-name="message"]');
        const form = document.querySelector('#contact');
        const newId = keyGen();

        const formData = {
            date: Date(),
            id: newId,
            data: {
                name: name.value,
                email: email.value,
                messageForm: messageForm.value
            }
        };
        const result = () => {
            const data = JSON.stringify(formData);
            let promise = new Promise((res, rej) => {
                (data) ? res(data) : rej("Error");
                
            });
            return promise;
        }
        await result()
            .then( data => {
                modalMessage('#contact', 'sucsses');
                this.setState({result: data})
                removeModal('.modalMes', 2000);
            })
            .catch(() => {
                modalMessage('#contact', "error");
                removeModal('.modalMes', 2000);
            })
            .finally(() => {
                form.reset();
            });
    }
    render() {
        const {result} = this.state;
        return (
            <div id='contactUs' className='contactUs'>
                <div className="contactUs__info" >
                    <div onClick={(e) => this.showTabContent(e)} data-id='map' className="contactUs__info-header-tab">Map</div>
                    <div className="contactUs__tabcontent">
                        <div className="map">
                            <iframe src={"https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d42329.693537937834!2d34.98335281031419!3d48.46409169147532!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x40dbe303fd08468f%3A0xa1cf3d5f2c11aba!2z0JTQvdC40L_RgNC-LCDQlNC90LXQv9GA0L7Qv9C10YLRgNC-0LLRgdC60LDRjyDQvtCx0LvQsNGB0YLRjCwgNDkwMDA!5e0!3m2!1sru!2sua!4v1595367739647!5m2!1sru!2sua"}></iframe>
                        </div>
                    </div>
                    <div onClick={(e) => this.showTabContent(e)} data-id='social' className="contactUs__info-header-tab">Social</div>
                    <div className="contactUs__tabcontent">
                        <ContactSocial/>
                    </div> 
                    <div onClick={(e) => this.showTabContent(e)} data-id='adress' className="contactUs__info-header-tab">Adress</div>
                    <div className="contactUs__tabcontent">
                        <ContactAdress/>
                    </div> 
                    <div onClick={(e) => this.showTabContent(e)} data-id='form' className="contactUs__info-header-tab">Form</div>
                    <div className="contactUs__tabcontent show-content">
                        <ContactForm sendForm={this.sendForm}/> 
                    </div> 
                    <div onClick={(e) => this.showTabContent(e)} className="contactUs__info-header-tab">Form result</div>
                    <div className="contactUs__tabcontent">
                        <div className="resylt-form">
                           <h5>What You send on form:</h5>
                            {(result !== null) ? <p>{`${result}`}</p> : false} 
                        </div>
                        
                    </div> 
                </div>
            </div>
        )
    }
}