import React, {Component} from 'react';
import keyGen from '../../../keyGenerator/keyGenerator';
import {SvgClose} from '../../social-icon/svgIcon';
import './deliveryList.css';


export default class DeliveryList extends Component {   

    render() {  
        
        const {orderList, removeDeliveryItem} = this.props;
        
        const createList = orderList.map((item, i) => {
            const {name, total, num} = item;
            return (
                <li key ={keyGen()}>
                    <div onClick={() => removeDeliveryItem(i)} className='del-list-remove'>
                        <SvgClose/>
                    </div>
                    <p>{`${name}`}</p>
                    <span>{`items: ${num}`}</span>
                    <div className='totalItem'>{`${total} $`}</div>
                </li> 
            );
        }); 

        
        return (
            <ul className='delivery__list-content'>
                {createList}    
            </ul>
        )
    }
}